window.addEventListener('load', function(e) {

	var search_button = document.querySelector('form .search_button');
	var search = document.querySelector('form .search');
	var mainmenu = document.querySelector('#main_menu');
	var burger = document.querySelector('#burger');

	search_button.addEventListener('click', function(e) {
		search.style.display = 'inline-block';
		mainmenu.style.display = 'none';
	});

	burger.onclick = dropMenu;

	function dropMenu(){
		document.querySelector(this.hash).classList.toggle('show');
		if(this.id == 'burger'){
			document.querySelector('.nav .wrap').classList.toggle('show');
		}
		return false;
	}

	$('.header .slider').owlCarousel({
		items: 1,
		loop: true
	});
	$('.section_offers .slider').owlCarousel({
		items: 1,
		loop: true
	});
	$('.section_review .slider').owlCarousel({
		items: 1,
		loop: true,
		nav: true,
		navText: ['<i class="fa fa-chevron-left" aria-hidden="true"></i>', '<i class="fa fa-chevron-right" aria-hidden="true"></i>']
	});
});